// pages/VaccineForm.tsx (or a component file, e.g., components/VaccineForm.tsx)
import Image from "next/image";
import { useState } from "react";

export default function VaccineForm() {
  // State to store form data
  const [formData, setFormData] = useState({
    childName: "",
    dateOfBirth: "",
    placeOfBirth: "",
    address: "",
    motherName: "",
    fatherName: "",
    birthHeight: "",
    birthWeight: "",
    sex: "",
    healthCenter: "",
    barangay: "",
    familyNo: "",
  });

  // Handle any change on an input field
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  return (
    <div className="relative w-full max-w-4xl mx-auto">
      {/* 
        The Image component renders the background form.
        Adjust width and height as needed.
      */}
      <Image
        src="/vaccine form.jpg"
        alt="Vaccine Form"
        width={1000}
        height={600}
        layout="responsive" // Ensures the image scales responsively
      />

      {/* 
        This container overlays the entire image.
        The absolute positioning ensures each input is placed relative to the image.
      */}
      <div className="absolute inset-0">
        {/* Child's Name */}
        <input
          type="text"
          name="childName"
          value={formData.childName}
          onChange={handleChange}
          placeholder="Child's Name"
          className="absolute top-[50px] left-[120px] w-[250px] border-b border-black bg-transparent text-black"
        />

        {/* Date of Birth */}
        <input
          type="date"
          name="dateOfBirth"
          value={formData.dateOfBirth}
          onChange={handleChange}
          className="absolute top-[100px] left-[120px] w-[250px] border-b border-black bg-transparent text-black"
        />

        {/* Place of Birth */}
        <input
          type="text"
          name="placeOfBirth"
          value={formData.placeOfBirth}
          onChange={handleChange}
          placeholder="Place of Birth"
          className="absolute top-[150px] left-[120px] w-[250px] border-b border-black bg-transparent text-black"
        />

        {/* Address */}
        <input
          type="text"
          name="address"
          value={formData.address}
          onChange={handleChange}
          placeholder="Address"
          className="absolute top-[200px] left-[120px] w-[400px] border-b border-black bg-transparent text-black"
        />

        {/* Mother's Name */}
        <input
          type="text"
          name="motherName"
          value={formData.motherName}
          onChange={handleChange}
          placeholder="Mother's Name"
          className="absolute top-[250px] left-[120px] w-[250px] border-b border-black bg-transparent text-black"
        />

        {/* Father's Name */}
        <input
          type="text"
          name="fatherName"
          value={formData.fatherName}
          onChange={handleChange}
          placeholder="Father's Name"
          className="absolute top-[300px] left-[120px] w-[250px] border-b border-black bg-transparent text-black"
        />

        {/* Birth Height */}
        <input
          type="text"
          name="birthHeight"
          value={formData.birthHeight}
          onChange={handleChange}
          placeholder="Birth Height"
          className="absolute top-[350px] left-[120px] w-[150px] border-b border-black bg-transparent text-black"
        />

        {/* Birth Weight */}
        <input
          type="text"
          name="birthWeight"
          value={formData.birthWeight}
          onChange={handleChange}
          placeholder="Birth Weight"
          className="absolute top-[350px] left-[300px] w-[150px] border-b border-black bg-transparent text-black"
        />

        {/* Sex */}
        <input
          type="text"
          name="sex"
          value={formData.sex}
          onChange={handleChange}
          placeholder="Sex"
          className="absolute top-[400px] left-[120px] w-[100px] border-b border-black bg-transparent text-black"
        />

        {/* Health Center */}
        <input
          type="text"
          name="healthCenter"
          value={formData.healthCenter}
          onChange={handleChange}
          placeholder="Health Center"
          className="absolute top-[400px] left-[250px] w-[200px] border-b border-black bg-transparent text-black"
        />

        {/* Barangay */}
        <input
          type="text"
          name="barangay"
          value={formData.barangay}
          onChange={handleChange}
          placeholder="Barangay"
          className="absolute top-[450px] left-[120px] w-[200px] border-b border-black bg-transparent text-black"
        />

        {/* Family No */}
        <input
          type="text"
          name="familyNo"
          value={formData.familyNo}
          onChange={handleChange}
          placeholder="Family No"
          className="absolute top-[450px] left-[350px] w-[150px] border-b border-black bg-transparent text-black"
        />
      </div>
    </div>
  );
}
